import React, { useRef, useEffect, useState } from 'react';
import './stylesheet/header.css';
import './stylesheet/Minigame.css';

function Minigame() {
  const canvasRef = useRef(null);
  const contextRef = useRef(null);
  const ws = useRef(null);
  const [isDrawing, setIsDrawing] = useState(false);
  const [prompt, setPrompt] = useState('cat');
  const [prediction, setPrediction] = useState('');
  const sendInterval = useRef(null);

  const prompts = ['_airplane', '_alarm clock', '_angel', '_ant', '_apple', '_axe', '_banana', '_bandage', '_barn', '_baseball bat', '_baseball', '_basket', '_basketball', '_bat', '_bee', '_bicycle', '_bird', '_bridge', '_butterfly', '_calculator', '_camel', '_camera', '_candle', '_car', '_carrot', '_cat', '_ceiling fan', '_chair', '_clock', '_cloud', '_compass', '_computer', '_cookie', '_crab', '_crown', '_diamond', '_dog', '_door', '_dragon', '_elephant', '_envelope', '_eye', '_fire hydrant', '_fish', '_flower', '_fork', '_giraffe', '_guitar', '_hammer', '_helicopter', '_hot air balloon', '_house', '_key', '_light bulb', '_monkey', '_mountain', '_mouth', '_octopus', '_palm tree', '_paper clip', '_pencil', '_piano', '_pineapple', '_rhinoceros', '_sandwich', '_scissors', '_shark', '_sheep', '_shoe', '_shorts', '_skateboard', '_skull', '_smiley face', '_snail', '_snowflake', '_snowman', '_soccer ball', '_sock', '_spider', '_star', '_stop sign', '_sun', '_sword', '_t-shirt', '_television', '_tennis racquet', '_The Eiffel Tower', '_The Great Wall of China', '_The Mona Lisa', '_tooth', '_toothbrush', '_train', '_tree', '_windmill'];

  useEffect(() => {
    const canvas = canvasRef.current;
    canvas.width = 600 * 2; // Increase for high DPI screens
    canvas.height = 400 * 2;
    canvas.style.width = `600px`;
    canvas.style.height = `400px`;

    const context = canvas.getContext('2d');
    context.scale(2, 2); // Adjust for high DPI screens
    context.lineCap = "round";
    context.strokeStyle = "black";
    context.lineWidth = 5;
    contextRef.current = context;

    ws.current = new WebSocket('ws://localhost:8000/ws/backend/');
    ws.current.onopen = () => console.log('WebSocket Connected');
    ws.current.onmessage = (e) => {
      const data = JSON.parse(e.data);
      setPrediction(data.prediction);
    };
    ws.current.onclose = () => console.log('WebSocket Disconnected');

    return () => {
      ws.current.close();
      clearInterval(sendInterval.current);
    };
  }, []);

  useEffect(() => {
    if (isDrawing) {
      sendInterval.current = setInterval(() => {
        sendCanvasImage();
      }, 2000); // Send the image every 2 seconds
    } else {
      clearInterval(sendInterval.current);
    }
    return () => clearInterval(sendInterval.current);
  }, [isDrawing]);

  const startDrawing = ({nativeEvent}) => {
    const {offsetX, offsetY} = nativeEvent;
    contextRef.current.beginPath();
    contextRef.current.moveTo(offsetX, offsetY);
    setIsDrawing(true);
  };

  const finishDrawing = () => {
    contextRef.current.closePath();
    setIsDrawing(false);
    sendCanvasImage();
  };

  const draw = ({nativeEvent}) => {
    if (!isDrawing) {
      return;
    }
    const {offsetX, offsetY} = nativeEvent;
    contextRef.current.lineTo(offsetX, offsetY);
    contextRef.current.stroke();
  };

  const sendCanvasImage = () => {
    const canvas = canvasRef.current;
    canvas.toBlob(blob => {
      if (ws.current.readyState === WebSocket.OPEN) {
        ws.current.send(blob);
      }
    }, 'image/jpeg', 0.75);
  };

  const clearCanvas = () => {
    const context = contextRef.current;
    context.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
    setPrediction('');
  };

  const resetGame = () => {
    clearCanvas();
    const newPrompt = prompts.filter(p => p !== prompt)[Math.floor(Math.random() * (prompts.length - 1))];
    setPrompt(newPrompt);
  };

  return (
    <div className="minigame-container" style={{ height: '700px' }}>
      <div className="section-header">
        <div><span className="section-hover-text section-hide">&nbsp;&nbsp;</span></div>
        <div><span className="section-hover-text" aria-hidden="true">Bonus</span></div>
        <div><span className="section-hover-text section-hide">&nbsp;&nbsp;</span></div>
      </div>

      <div className='minigame'>
        <h4>Prompt: Draw a {prompt} &nbsp;&nbsp;&nbsp;&nbsp;Prediction: {prediction}</h4>

        <canvas
          onMouseDown={startDrawing}
          onMouseMove={draw}
          onMouseUp={finishDrawing}
          ref={canvasRef}
        />

        <div className="game-controls">
          <button onClick={clearCanvas}>Clear</button>
          <button onClick={resetGame}>New Prompt</button>
        </div>
      </div>
    </div>
  );
}

export default Minigame;
